import React from 'react'
import { connect } from 'react-redux'
import { Spin } from 'antd'

const Highcharts = require('./highcharts.src.js')
require('./adapt-chart-to-legend.js')(Highcharts)

import './Charts.scss'

class Chart extends React.Component {
  componentDidMount() {
    this.chart = new Highcharts["Chart"](
      this.refs.chart,
      this.props.options
    )
  }
  // rsprspflagrsp
  shouldComponentUpdate(nextProps) {
    if (nextProps.len !== this.props.len) {
      return true
    }
    return false
  }
  componentDidUpdate() {
    this.chart = new Highcharts["Chart"](
      this.refs.chart,
      this.props.options
    )
  }
  componentWillUnmount() {
    this.chart.destroy()
  }
  render() {
    return (
      <div ref="chart" />
    )
  }
}

class ShowCharts extends React.PureComponent {
  render() {
    const { chartsData, isLoading, title, start, end } = this.props.dashboard.toJS()
    /*
      1.highcharts
      2.chartlegendtooltipdata
    */
    Highcharts.setOptions({
      chart: {
        zoomType: 'x'
      },
      colors: ['#7cb5ec', '#434348', '#90ed7d', '#f7a35c', '#8085e9',
        '#f15c80', '#e4d354', '#2b908f', '#f45b5b', '#91e8e1', '#2f7ed8', '#0d233a', '#8bbc21', '#910000', '#1aadce',
        '#492970', '#f28f43', '#77a1e5', '#c42525', '#a6c96a', '#4572A7', '#AA4643', '#89A54E', '#80699B', '#3D96AE',
        '#DB843D', '#92A8CD', '#A47D7C', '#B5CA92'],
      global: {
        useUTC: false,
      },
      credits: {
        enabled: false
      },
      xAxis: [{
        type: 'datetime',
        dateTimeLabelFormats: {
          hour: '%H:%M',
        },
        max: end * 1000,
        tickInterval: ((end - start) / 12) * 1000,
        labels: {
          step: 1,
        },
        tickPosition: 'inside',
      }],
      plotOptions: {
        series: {
          marker: {
            radius: 1,
          },
          pointStart: start * 1000,
          lineWidth: 1,
        },
      }
    })

    /*
      1.chartsDataviews
      2.chartsData
      2.1
      2.2endpointcounterValuesnull[]chartseries{name}
      2.3Valuesnullchartsseries{name,data}
    */
    let views = ''
    if (chartsData.length !== 0) {
      views = chartsData.map((eachRspObj, index) => {
        const eachRsp = eachRspObj.rsp
        // []legend
        if (eachRsp.length === 0) {
          const { counters, hostnames } = eachRspObj.params
          let chartTitle
          let name = [{ name: '  ' }]
          let isLegend = true
          if (title === 'Endpoint') {
            chartTitle = hostnames[0]
            name = counters.map((counter) => {
              return { name: counter }
            })
          } else if (title === 'Counter') {
            chartTitle = counters[0]
            name = hostnames.map((hostname) => {
              return { name: hostname }
            })
          } else {
            chartTitle = ''
            isLegend = false
          }
          const options = {
            title: {
              useHTML: true,
              text: `<p class="dashboard-chart-title">${chartTitle}</p>`,
            },
            series: name,
            chart: {
              height: 230,
            },
            legend: {
              enabled: isLegend,
              verticalAlign: 'bottom',
              adjustChartSize: true,
              navigation: {
                enabled: false
              },
              borderWidth: 0,
              symbolWidth: 10,
              useHTML: true,
              labelFormatter: function() {
                const chartWidth = this.chart.chartWidth
                const legendWidth = Math.ceil(chartWidth * 0.8)
                return `<div class="dashboard-legend-name" style="width:${legendWidth}px">${this.name}</div>`
              },
            },
          }
          return (<div key={index} className="charts">
            <Chart container={'chart'} options={options} len={eachRsp.length} />
          </div>)
        } else {
          // Valuesnull[]seriesnamedata
          const results = eachRsp.filter(obj => obj.Values !== null && obj.Values.length !== 0)
          if (results.length === 0) {
            let chartTitle, name
            if (title === 'Endpoint') {
              chartTitle = eachRsp[0].counter
              name = eachRsp.map(eachObj => {
                const newObj = {}
                newObj.name = eachObj.endpoint
                return newObj
              })
            } else if (title === 'Counter') {
              chartTitle = eachRsp[0].endpoint
              name = eachRsp.map(eachObj => {
                const newObj = {}
                newObj.name = eachObj.counter
                return newObj
              })
            } else {
              chartTitle = ''
              name = eachRsp.map(eachObj => {
                const newObj = {}
                newObj.name = `${eachObj.endpoint} | ${eachObj.counter}`
                return newObj
              })
            }
            // namechartTitlechartoptions
            const options = {
              title: {
                useHTML: true,
                text: `<p class="dashboard-chart-title">${chartTitle}</p>`,
              },
              series: name,
              chart: {
                height: 230,
              },
              legend: {
                enabled: true,
                verticalAlign: 'bottom',
                adjustChartSize: true,
                navigation: {
              	  enabled: false
                },
                borderWidth: 0,
                symbolWidth: 10,
                useHTML: true,
                labelFormatter: function() {
                  const chartWidth = this.chart.chartWidth
                  const legendWidth = Math.ceil(chartWidth * 0.8)
                  return `<div class="dashboard-legend-name" style="width:${legendWidth}px">${this.name}</div>`
                },
              },
            }
            return (<div key={index} className="charts">
              <Chart container={'chart'} options={options} len={eachRsp.length} />
            </div>)
          } else {
            // 
            const { method } = this.props.dashboard.toJS()
            let chartTitle, data
            if (title === 'Endpoint') {
              chartTitle = eachRsp[0].counter
              data = eachRsp.map((eachObj) => {
                const newObj = {}
                newObj.name = eachObj.endpoint
                if (eachObj.Values !== null && eachObj.Values.length !== 0) {
                  newObj.data = eachObj.Values.map((eachValue) => {
                    eachValue.timestamp = eachValue[0]
                    eachValue.value = eachValue[1]
                    return [eachValue.timestamp * 1000, eachValue.value]
                  })
                }
                return newObj
              })
            } else if (title === 'Counter') {
              chartTitle = eachRsp[0].endpoint
              data = eachRsp.map((eachObj) => {
                const newObj = {}
                newObj.name = eachObj.counter
                if (eachObj.Values !== null && eachObj.Values.length !== 0) {
                  newObj.data = eachObj.Values.map((eachValue) => {
                    eachValue.timestamp = eachValue[0]
                    eachValue.value = eachValue[1]
                    return [eachValue.timestamp * 1000, eachValue.value]
                  })
                }
                return newObj
              })
            } else {
              chartTitle = ''
              data = eachRsp.map((eachObj) => {
                const newObj = {}
                newObj.name = `${eachObj.endpoint} | ${eachObj.counter}`
                if (eachObj.Values !== null && eachObj.Values.length !== 0) {
                  newObj.data = eachObj.Values.map((eachValue) => {
                    eachValue.timestamp = eachValue[0]
                    eachValue.value = eachValue[1]
                    return [eachValue.timestamp * 1000, eachValue.value]
                  })
                }
                return newObj
              })
            }
            // sumonlysumonlysumdatasum
            if (method === 'sum' || method === 'onlysum') {
              /*
                1.sumResults = [[v, v, ...], [v, v, ...]...]
                2.Valuesnull[]
              */
              const filterResults = eachRsp.filter(eachObj => eachObj.Values !== null && eachObj.Values.length !== 0)
              const sumResults = filterResults.map((eachObj) => {
                return eachObj.Values.map(eachValue => eachValue.value)
              })
              // sumData = [sum1, sum2, ...],sumResults
              let maxIndex = 0
              const sumData = sumResults.reduce((acc, curValue, curIndex) => {
                if (acc.length <= curValue.length) {
                  maxIndex = curIndex
                  return curValue.map((item, curIndex) => {
                    if ((item === null || item === undefined)
                    && (acc[curIndex] === null || acc[curIndex] === undefined)) {
                      return null
                    }
                    return (item || 0) + (acc[curIndex] || 0)
                  })
                }
                return acc.map((item, accIndex) => {
                  if ((item === null || item === undefined)
                  && (curValue[accIndex] === null || curValue[accIndex] === undefined)) {
                    return null
                  }
                  return (item || 0) + (curValue[accIndex] || 0)
                })
              }, [])
              // sum = [[timestamp, sum1], [timestamp, sum2]...]
              const sum = sumData.map((item, sumIndex) => {
                return [filterResults[maxIndex].Values[sumIndex].timestamp * 1000, item]
              })
              if (method === 'sum') {
                data.push({ name: 'sum', data: sum, color: '#FF0000' })
              } else {
                data = [{ name: 'sum', data: sum, color: '#FF0000' }]
              }
            }
            data.unshift({
              name:' ',
              color: 'transparent',
              events: {
                legendItemClick: function () {
                  return false
                },
              },
            })
            // datachartTitlechartoptions
            const options = {
              title: {
                useHTML: true,
                text: `<p class="dashboard-chart-title">${chartTitle}</p>`,
              },
              series: data,
              chart: {
                height: 230,
              },
              tooltip: {
                xDateFormat: '%Y-%m-%d %H:%M:%S',
                borderWidth: 0,
                backgroundColor: "rgba(175,216,248, 0.65)",
                shadow: false,
              },
              legend: {
                enabled: true,
                verticalAlign: 'bottom',
                adjustChartSize: true,
                navigation: {
              	  enabled: false
                },
                borderWidth: 0,
                symbolWidth: 10,
                useHTML: true,
                labelFormatter: function() {
                  const chartWidth = this.chart.chartWidth
                  const legendWidth = Math.ceil(chartWidth * 0.95)
                  const nameWidth = Math.ceil(legendWidth * 0.5)
                  const numWidth = Math.floor(chartWidth * 0.07)
                  if (this.index === 0) {
                    return `<div class="dashboard-legend-container" style="width:${legendWidth}px">
                      <div class="dashboard-legend-name" style="width:${nameWidth}px">${this.name}</div>
                      <div class="dashboard-legend-num" style="margin-left: 10px">
                        <span style="color: #DB7093;width:${numWidth}px"> last </span>
                        <span style="color: #FF00FF;width:${numWidth}px"> min </span>
                        <span style="color: #9932CC;width:${numWidth}px"> max </span>
                        <span style="color: #DC143C;width:${numWidth}px"> avg </span>
                      </div>
                    </div>`
                  }
                  // this.yData[]this.yDatanull
                  const yData = this.yData.filter(item => item !== null)
                  const len = yData.length
                  let max = 'NAN'
                  let min = 'NAN'
                  let last = 'NAN'
                  let avg = 'NAN'
                  // Valuesvaluenull
                  // legend
                  const suffix = ['', 'K', 'M', 'G', 'T', 'P']
                  const formatNum = (num) => {
                    let count = 0
                    while (num/1000 >= 1) {
                      num = num/1000
                      count += 1
                    }
                    num = num.toFixed(3)
                    return num + suffix[count]
                  }
                  if (len !== 0) {
                    max = Math.max(...yData)
                    min = Math.min(...yData)
                    last = yData[len - 1]
                    const sum = yData.reduce((pre, cur) => cur += pre)
                    avg = (sum / len)
                    max = formatNum(max)
                    min = formatNum(min)
                    last = formatNum(last)
                    avg = formatNum(avg)
                  }
                  return `<div class="dashboard-legend-container" style="width:${legendWidth}px">
                    <div class="dashboard-legend-name" style="width:${nameWidth}px">${this.name}</div>
                    <div class="dashboard-legend-num" style="margin-left: 10px">
                      <span style="color: #DB7093;width:${numWidth}px"> ${last} </span>
                      <span style="color: #FF00FF;width:${numWidth}px"> ${min} </span>
                      <span style="color: #9932CC;width:${numWidth}px"> ${max} </span>
                      <span style="color: #DC143C;width:${numWidth}px"> ${avg} </span>
                    </div>
                  </div>`
                },
              },
            }
            return (<div key={index} className="charts">
              <Chart container={'chart'} options={options} />
            </div>)
          }
        }
      })
    }
    return (
      <div className="charts-container">
        {isLoading === 0 ? <div className="dashboard-showcharts-loading"><Spin size="large" /></div> : ''}
        {views}
      </div>
    )
  }
}

const mapStateToProps = state => ({
  dashboard: state.dashboard,
})

export default connect(mapStateToProps)(ShowCharts)



