import React, { PureComponent } from 'react'
import { withRouter } from 'react-router'
import { connect } from 'react-redux'
import { Form, Row, DatePicker, Select, Checkbox, Button, Icon } from 'antd'
import moment from 'moment'
import ShowCharts from './ShowCharts'

import { getCharts, saveSearchData, getChartParams } from '../modules/index'
import './Charts.scss'

const Option = Select.Option
const FormItem = Form.Item

class Charts extends PureComponent {
  state = {
    title: '',
    cf: 'AVERAGE',
    start: '',
    end: '',
    method: 'nosum',
  }
  componentDidMount() {
    const { id, graph_type, start, end, cf, method } = this.props.location.query
    // idendpointscounters
    this.props.getChartParams(id, graph_type, Number(start), Number(end), cf, method)
    // URL
    let title = 'Endpoint'
    if (graph_type === 'k') {
      title = 'Counter'
    }
    if (graph_type === 'a') {
      title = ''
    }
    this.setState({
      title,
      cf,
      start: moment.unix(start),
      end: moment.unix(end),
      method: (method === undefined) ? 'nosum' : method,
    })
  }
  handleSum = (value) => {
    this.setState({ method: value })
  }
  onStartChange = (value) => {
    this.setState({start: value})
  }
  onStartOk = (value) => {
    this.setState({start: value})
  }
  onEndChange = (value) => {
    this.setState({end: value})
  }
  onEndOk = (value) => {
    this.setState({end: value})
  }
  handlecfChange = (value) => {
    this.setState({cf: value})
  }
  handleViewChange = (value) => {
    this.setState({title: value})
  }
  handleReset = () => {
    this.setState({
      title: 'Endpoint',
      cf: 'AVERAGE',
      start: moment().subtract(1, 'hour'),
      end: moment(),
      method: 'nosum',
    })
  }
  showCharts = () => {
    const { id, selectedEndpoints, selectedCounters, start, end } = this.props.dashboard.toJS()
    const cf = this.state.cf
    const title = this.state.title
    const method = this.state.method

    let startUnix = this.state.start.unix()
    let endUnix = this.state.end.unix()
    // 
    if (start === startUnix && end === endUnix) {
      startUnix = moment().subtract(1, 'hour').unix()
      endUnix = moment().unix()
      this.setState({
        start: moment().subtract(1, 'hour'),
        end: moment(),
      })
    }
    // storeURL
    const searchData = {
      id,
      endpoints: selectedEndpoints,
      counters: selectedCounters,
      title,
      startTime: startUnix,
      endTime: endUnix,
      cf,
      method,
    }
    this.props.saveSearchData(searchData)
    this.props.getCharts(cf, selectedEndpoints, selectedCounters, title, startUnix, endUnix)
    let graph_type = 'h'
    if (title === 'Counter') {
      graph_type = 'k'
    }
    if (title === '') {
      graph_type = 'a'
    }
    const data = {id, graph_type, cf, start: startUnix, end: endUnix, method: (method === 'nosum') ? undefined : method}
    const path = {
      pathname: '/dashboard/charts',
      query: data,
    }
    this.props.router.push(path)
  }

  render() {
    const { title, start, end, cf, method } = this.props.dashboard.toJS()
    const dateFormat = 'YYYY-MM-DD HH:mm:ss'
    return (
      <div className="canvas">
        <p className="header">
          {title} 
        </p>
        <Form layout='inline' id="charts-form">
          <FormItem label="">
            <DatePicker
              showTime
              format={dateFormat}
              onChange={this.onStartChange}
              onOk={this.onStartOk}
              value={this.state.start}
            />
          </FormItem>
          <FormItem label="">
            <DatePicker
              showTime
              format="YYYY-MM-DD HH:mm:ss"
              onChange={this.onEndChange}
              onOk={this.onEndOk}
              value={this.state.end}
            />
          </FormItem>
          <FormItem label="">
            <Select style={{ width: 100 }} onChange={this.handlecfChange} value={this.state.cf}>
              <Option value="AVERAGE">AVERAGE</Option>
              <Option value="MAX">MAX</Option>
              <Option value="MIN">MIN</Option>
            </Select>
          </FormItem>
          <FormItem label="">
            <Select style={{ width: 100 }} onChange={this.handleViewChange} value={this.state.title}>
              <Option value="Endpoint">Endpoint</Option>
              <Option value="Counter">Counter</Option>
              <Option value=""></Option>
            </Select>
          </FormItem>
          <FormItem label="">
            <Select style={{ width: 100 }} onChange={this.handleSum} value={this.state.method}>
              <Option value="sum"></Option>
              <Option value="onlysum"></Option>
              <Option value="nosum"></Option>
            </Select>
          </FormItem>
          <FormItem>
            <Button type="primary" onClick={this.showCharts}></Button>
          </FormItem>
          <FormItem>
            <Button type="primary" onClick={this.handleReset}></Button>
          </FormItem>
        </Form>
        <ShowCharts />
      </div>
    )
  }
}

const mapActionCreators = {
  getCharts,
  saveSearchData,
  getChartParams,
}
const mapStateToProps = state => ({
  dashboard: state.dashboard,
})
export default withRouter(connect(mapStateToProps, mapActionCreators)(Charts))



